.gallery-thumbs, #panel_second {
  .views-field-field-image {
    margin: 0 5px 15px 0;

    img {
      background-color: $white;
      padding: 3px;
      border: 3px solid #555;
    }

    a {
      &:hover, &:focus {
        img {
          border-style: dashed;
        }
      }
    }
  }
}

.view-node {
  .views-field-created {
    display: block;
    margin: 0 0 15px;
    overflow: hidden;
    padding: 0;
    width: 100%;
    span {
      display: block;
      float: right;
      height: 53px;
      overflow: hidden;
      padding: 9px 0 0;
      width: 103px;
    }
  }

  .views-field-created {
    display: block;
    margin: 0 0 15px;
    overflow: hidden;
    padding: 0;
    width: 100%;
    .field-content {
      display: block;
      float: right;
      height: 53px;
      overflow: hidden;
      padding: 15px 0 0;
      width: 103px;
      color: $white;
      font-size: 15px;
    }

    .date-left {
      display: block;
      float: left;
      font-family: 'oswaldregular';
      font-size: 2em;
      line-height: 1.2;
      text-align: center;
      width: 49px;
    }

    .date-right {
      border-left: 1px solid #999;
      display: block;
      float: left;
      line-height: 1.2;
      text-align: center;
      text-transform: uppercase;
      width: 53px;
    }
  }

  .views-field-title {
    font-size: 2em;
    //width: 80%;
    line-height: 1.4;
    margin: -40px 0 30px;
    padding-right: 130px;
    font-weight: 500;
  }

  .views-field-nothing {
    border: 0 none;
    margin-bottom: 30px;
    padding: 28px;
    position: relative;
  }

  .field-author {
    margin-bottom: 14px;
  }

  .field-img {
    margin-bottom: 1em;
  }

  .group-field-link {
    margin: 0 10px 5px 0;
    padding: 0;

    a {
      border-bottom: 1px dotted $red;
      margin: {
        right: 10px;
        bottom: 5px;
      };

      &:before {
        content: "+";
        margin-right: 5px;
      }
      &:hover, &:focus{
        border-bottom-color: #fff;
      }
    }
  }
}

.view-node {
  .views-field-created {
    span {
      text-align: center;
      font-family: "oswaldregular";
      text-transform: uppercase;
    }
  }
}

.page #main .content .node h2.comment-form__title {
  margin-top: 0;
}

.page #main #main-content {
  .gallery-list {
    .views-col {
      float: left;
    }

    .view-row {
      @include clearfix;

      margin-bottom: 20px;
    }

    .views-field-title {
      margin: 0 10px;
      padding: 15px;
      text-align: center;
      background: #333;
      border: 1px solid #444;

      img {
        margin-bottom: 10px;
      }

      a {
        min-height: 42px;
      }

      &:hover, &:focus {
        border-color: #666;

        @include box-shadow(0 0 5px #000000);
      }
    }
  }
}

.view.view-frontpage, 
.view-frontpage-header,  
.block-views-blockrelated-articles-block-1{
  .feed-icon{
    display: none;
  }

  .view-header {
    font-size: 60px;
    text-align: center;
    font-weight: bold;
    color: #000000;
    display: block;
    position: relative;
    margin-bottom: 20px;

    &:after, &:before {
      content: " ";
      position: absolute;
      border: 1px solid $gold;
      width: 33%;
      height: 1px;
    }

    &:before {
      top: 50%;
      right: 0;
    }

    &:after {
      bottom: 50%;
      left: 0;
    }
  }

  .view-content {
    margin-bottom: 60px;

    .field--name-node-title {
      display: block;
      position: relative;
      margin-bottom: 30px;

      &:after, &:before {
        content: " ";
        position: absolute;
        border: 1px solid #DDDDDD;
        width: 13%;
        height: 1px;
      }

      &:before {
        top: 50%;
        right: 0;
      }

      &:after {
        bottom: 50%;
        left: 0;
      }

      h2 {
        text-align: center;
        width: 70%;
        margin: 0 auto;

        a {
          line-height: 33px;
          color: #000000;
          font-size: 29px;
        }
      }
    }

    .field--name-field-women-in-this-article {
      margin-top: -50px;
      margin-right: 30px;

      .field--name-field-photo {
        img {
          border-radius: 50%;
          overflow: hidden;
          margin: 0;
        }
      }

      .field--name-node-title {
        margin-top: -30px;
        margin-right: 70px;

        &:before, &:after {
          content: none;
        }  
        h3 {
          font-size: 14px;
        }
      }
    }

    .field--name-node-post-date {
      float: right;
    }
    .node__links {
      float: left;
    }

    .field--name-body {
      p {
        text-align: center;
        padding: 0 15px;
        font-size: 17px;
      }
    }
  }
}


.view-frontpage-footer{
  .field--name-field-women-in-this-article {

    .field--name-field-photo {
      img {
        border-radius: 50%;
        overflow: hidden;
        margin: 0;
      }
    }

    .field--name-node-title {
      margin-top: -30px;
      margin-right: 70px;

      &:before, &:after {
        content: none;
      }  
      h3 {
        font-size: 14px;
      }
    }
  }
}
.view-frontpage-header::before {
  @include page_gold_border(1);
}

.view-frontpage::before {
  @include page_gold_border(0.2);
}

#panel_second .row::before {
  @include page_gold_border(0.1);
}